@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/* SVG Text Styling */
.svg-text,
.svg-subtext {
  font-family: "Poppins", sans-serif;
  fill: none;
  stroke: #ffffff;
  stroke-width: 1px;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  animation: draw 6s forwards ease-in-out, fill 4s forwards ease-in-out;
}

/* Animation for typing */
@keyframes draw {
  to {
    stroke-dashoffset: 0;
  }
}

/* Animation for fill */
@keyframes fill {
  from {
    fill: transparent;
  }
  to {
    fill: currentColor;
  }
}

/* SVG Text Hover Effect */
.svg-text:hover {
  fill: #ffcc29;
}

.name-text:hover {
  fill: #ffafcc;
}

.svg-subtext:hover {
  fill: #ffcc29;
}

.typewriter {
  overflow: hidden;
  border-right: 0.15em solid #ffffff;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.15em;
  animation: typing 1.5s steps(10, end), blink-caret 0.9s step-end infinite;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 10%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #ffffff;
  }
}
